import React from "react";
import fotoPerfil from "../pics/perfil.png";
class ProfessionalSkills extends React.Component {
  background = {
    textShadow: "1px 1px #000000"
  };
  render() {
    return (
      <React.Fragment>
        <div className="card shadow rounded-lg">
          <div
            id="carouselExampleCaptions"
            className="carousel slide carousel-fade"
            data-ride="carousel"
          >
            <ol className="carousel-indicators">
              <li
                data-target="#carouselExampleCaptions"
                data-slide-to="0"
                className="active"
              />
              <li data-target="#carouselExampleCaptions" data-slide-to="1" />
              <li data-target="#carouselExampleCaptions" data-slide-to="2" />
              <li data-target="#carouselExampleCaptions" data-slide-to="3" />
              <li data-target="#carouselExampleCaptions" data-slide-to="4" />
              <li data-target="#carouselExampleCaptions" data-slide-to="5" />
              <li data-target="#carouselExampleCaptions" data-slide-to="6" />
              <li data-target="#carouselExampleCaptions" data-slide-to="7" />
            </ol>
            <div className="carousel-inner">
              <div className="carousel-item active">
                <img src={fotoPerfil} className="d-block w-100" alt="..." />
                <div className="carousel-caption" style={this.background}>
                  <h5>Capacidad de adaptación</h5>
                  <p>
                    Saber adaptarse a nuevas situaciones. Asumir otros roles.
                    Desempeñar nuevas funciones.
                  </p>
                </div>
              </div>
              <div className="carousel-item">
                <img src={fotoPerfil} className="d-block w-100" alt="..." />
                <div className="carousel-caption" style={this.background}>
                  <h5>Colaboración</h5>
                  <p>
                    Compartir, participar y colaborar con otros compañeros o
                    departamentos y alcanzar las metas fijadas.
                  </p>
                </div>
              </div>
              <div className="carousel-item">
                <img src={fotoPerfil} className="d-block w-100" alt="..." />
                <div className="carousel-caption" style={this.background}>
                  <h5>Toma de decisiones</h5>
                  <p>
                    Búsqueda y valoración de las alternativas a los diversos
                    problemas.
                  </p>
                </div>
              </div>
              <div className="carousel-item">
                <img src={fotoPerfil} className="d-block w-100" alt="..." />
                <div className="carousel-caption" style={this.background}>
                  <h5>Organización</h5>
                  <p>Habilidad para planificar y priorizar tareas</p>
                </div>
              </div>
              <div className="carousel-item">
                <img src={fotoPerfil} className="d-block w-100" alt="..." />
                <div className="carousel-caption" style={this.background}>
                  <h5>Idiomas</h5>
                  <p>Nivel B1 en inglés.</p>
                </div>
              </div>
              <div className="carousel-item">
                <img src={fotoPerfil} className="d-block w-100" alt="..." />
                <div className="carousel-caption" style={this.background}>
                  <h5>Creatividad e innovación</h5>
                  <p>
                    Crear e innovar en mí desempeño y consecución de objetivos.
                  </p>
                </div>
              </div>
              <div className="carousel-item">
                <img src={fotoPerfil} className="d-block w-100" alt="..." />
                <div className="carousel-caption" style={this.background}>
                  <h5>Proactividad</h5>
                  <p>
                    Iniciativa en el desarrollo de acciones creativas y audaces
                    para generar mejoras.
                  </p>
                </div>
              </div>
              <div className="carousel-item">
                <img src={fotoPerfil} className="d-block w-100" alt="..." />
                <div className="carousel-caption" style={this.background}>
                  <h5>Autodidacta</h5>
                  <p>
                    No todo está hecho con un título universitario, hay que
                    mantenerse siempre al tanto de nuestro sector laboral.
                  </p>
                </div>
              </div>
            </div>
            <a
              className="carousel-control-prev"
              href="#carouselExampleCaptions"
              role="button"
              data-slide="prev"
            >
              <span className="carousel-control-prev-icon" aria-hidden="true" />
              <span className="sr-only">Previous</span>
            </a>
            <a
              className="carousel-control-next"
              href="#carouselExampleCaptions"
              role="button"
              data-slide="next"
            >
              <span className="carousel-control-next-icon" aria-hidden="true" />
              <span className="sr-only">Next</span>
            </a>
          </div>
          <div className="text-warning font-weight-bold h3 mt-4 text-center">
            Habilidades profesionales
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default ProfessionalSkills;
