import React from "react";
import fotoPerfil from "../pics/perfil.png";
import isMobile from "ismobilejs";

class PersonalInfo extends React.Component {
  handleFB(e) {
    if (isMobile.phone) window.open("fb://profile/10201391213459338", "_blank");
    else window.open("https://www.facebook.com/Richi0210", "_blank");
  }

  handleTT(e) {
    if (isMobile.phone)
      window.open("twitter://user?screen_name=Ricardo_2R1", "_blank");
    else window.open("https://twitter.com/Ricardo_2R1", "_blank");
  }

  handleG(e) {}

  handleWA(e) {
    window.open("https://api.whatsapp.com/send?phone=5215551643219", "_blank");
  }
  //https://web.facebook.com/photo.php?fbid=10201391213459338&set=picfp.1744355384&type=3&eid=ARDFxoCsgthA7XmGDgKf77jktOH0AYFYgl3kF-iHGyVDR9Kx7JdKknsY8Mjv3xBVpZt1XyrOAY-jf1GW
  render() {
    return (
      <React.Fragment>
        <br />
        <div className="card shadow rounded-lg">
          <img
            src={fotoPerfil}
            alt="Foto de perfil"
            className="card-img-top img-fluid"
          />
          <div className="card-body text-center">
            <p className="text-warning font-weight-bold h3">
              Ricardo Torres Segura
            </p>
            <p className="font-weight-bold h4 text-primary">
              Full Stack Developer
            </p>
            <p className="text-info">ricardots211@gmail.com | 55 5164 3219</p>
            <blockquote className="blockquote">
              <p>
                La función de un buen software es hacer que lo complejo aparente
                ser simple
              </p>
              <footer className="blockquote-footer">Grady Booch</footer>
            </blockquote>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default PersonalInfo;
